import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { CoursedetailsService } from './../coursedetails.service';

@Component({
  selector: 'coursetable',
    template: `
        <h2>{{ title }}</h2>
        <ul class="table">
            <li class="tab" (click)="onSelect(course.subject)" *ngFor = "let course of courses">
                {{ course.subject | uppercase }}  : {{ course.code }} | {{ course.college }}
            </li>
        </ul>
    `   
})
export class CoursetableComponent implements OnInit {
  title = "Course Table";
  courses = [];
    // errorMsg;

  constructor(private _coursedetailsService: CoursedetailsService,
              private router: Router) { }

  ngOnInit() {
    this._coursedetailsService.getCourses()
        .subscribe(data => this.courses = data);
                    // error => this.errorMsg = error);

}

  onSelect(subject){
    this.router.navigate(['/coursetable', subject]);
  }

}
