import { HometabComponent } from './hometab/hometab.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CoursetableComponent } from './coursetable/coursetable.component';
import { CoursesComponent } from './courses.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { CourseviewComponent } from './courseview/courseview.component';
import { DurationComponent } from './duration/duration.component';
import { CostComponent } from './cost/cost.component';

const routes: Routes = [
  {path: '', redirectTo: '/hometab', pathMatch: 'full'},
  {path: 'hometab', component: HometabComponent},
  {
    path: 'coursetable/:subject', 
    component: CourseviewComponent,
    children: [
      {path: 'duration', component: DurationComponent},
      {path: 'cost', component: CostComponent}
    ]
  },
  {path: 'coursetable', component: CoursetableComponent},
  {path: 'courses', component: CoursesComponent},
  {path: '**', component: PageNotFoundComponent}  

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents = [HometabComponent, 
                                  CoursetableComponent, 
                                  CoursesComponent, 
                                  PageNotFoundComponent,
                                  CourseviewComponent,
                                  DurationComponent,
                                  CostComponent]
